#Guage #background-circle {
  fill: none;
  stroke: hsl(var(--base-hue), 100%, 75%);
  stroke-width: 1;
}

#Guage #range {
  fill: hsl(var(--base-hue), 100%, 15%);
  stroke: hsl(var(--base-hue), 100%, 75%);
}

#Guage #needle {
  fill: hsl(calc(var(--base-hue) - 210), 100%, 50%);
  stroke: hsl(calc(var(--base-hue) - 210), 100%, 75%);
}

#Guage text {
  text-anchor: middle;
  dominant-baseline: middle;
}

#Guage text#value {
  fill: hsl(calc(var(--base-hue) - -180), 100%, 50%);
  font-size: 72px;
  font-weight: bold;
}

#Guage text#title {
  fill: hsl(var(--base-hue), 100%, 75%);
  font-size: 48px;
}
